<template>
  <div class="">
    <el-col :span="24">
      <h1 class="title"></h1>
    </el-col>
    <el-col :span="24">
      <!--    -->
      <div v-for="(i,index) in numsInfo">
        <upload-file style="justify-content: flex-start;" :value="i.value" :keys="i.key"
                     @dialog="visible"></upload-file>
      </div>
    </el-col>
    <!--    -->
    <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="30%"
        :before-close="handleClose" :on-success="handleAvatarSuccess">
      <span></span>
      <up-load-pop @typeId_first="typeId" @typeId_second="typeId"></up-load-pop>
      <el-upload multiple
                 class="upload-demo" style="margin-top: 10px"
                 drag
                 list-type="picture"
                 :action="this.$config_upLoad"
                 :on-success="handleAvatarSuccess"
                 :before-upload="beforeAvatarUpload">
        <i class="el-icon-upload"></i>
        <div class="el-upload__text"><em></em></div>
        <!--        <div class="el-upload__tip" slot="tip">jpg/png4M</div>-->
      </el-upload>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="sure()"> </el-button>
      </span>
    </el-dialog>

    <div class="equipment">
      <!--    -->
      <div class="title01">
        <el-popconfirm title="TT  " @confirm="deleteFromData">
          <el-button slot="reference" type="primary" :disabled="disabled"></el-button>
        </el-popconfirm>
        <el-button @click="recall()" v-if="withdraw" type="warning" style="margin-right: 30px;"></el-button>
        <!--        <el-input style="margin:0 30px;"-->
        <!--                  placeholder=""-->
        <!--                  v-model="inputSearch"-->
        <!--                  clearable>-->
        <!--        </el-input>-->
        <!--        <el-button type="primary" icon="el-icon-search" @click="search()"></el-button>-->
        <light-btn :colors="btns" @lightByValue="lightByValue"></light-btn>
        <!--        <div class="btns all headBtn" @click="lightByValue(0)">-->
        <!--          <span></span>-->
        <!--        </div>-->
        <!--    -->
        <el-col :span="12" style="padding-left:10px;display: flex;align-items: center;">
          <strong></strong>
          <up-load-pop @typeId_first="typeId" @typeId_second="typeId"></up-load-pop>
        </el-col>
      </div>
      <!---->
      <el-col :span="24">
        <el-table
            border
            :data="tableData"
            @selection-change="handleSelectionChange"
            style="width: 100%;margin-top:30px">
          <el-table-column
              type="selection"
              width="55">
          </el-table-column>
          <!--          <el-table-column-->
          <!--              prop="headimage_id"-->
          <!--              label=""-->
          <!--              width="">-->
          <!--          </el-table-column>-->
          <!--          <el-table-column-->
          <!--              prop="password"-->
          <!--              label=""-->
          <!--              width="90">-->
          <!--          </el-table-column>-->
          <el-table-column
              prop="type_title"
              label=""
              width="">
          </el-table-column>
          <!--          <el-table-column-->
          <!--              prop="name"-->
          <!--              label=""-->
          <!--              width="100">-->
          <!--          </el-table-column>-->
          <el-table-column
              prop="status"
              label=""
              width="">
            <template slot-scope="scope">
              <!--              {{scope.row.status}}-->
              <statusLight :light="parseInt(scope.row.status)"></statusLight>
<!--              <el-button @click="recall()" v-if="scope.row.status==1" type="text" style="margin-right: 30px;">-->
<!--              </el-button>-->
            </template>
          </el-table-column>
          <el-table-column
              label=""
              width="">
            <template slot-scope="scope">
              <el-image
                  style="width: 60px;height: 60px;"
                  :src="scope.row.image"
                  fit="cover"></el-image>
            </template>
          </el-table-column>
          <!--          <el-table-column-->
          <!--              prop="number"-->
          <!--              label="//">-->
          <!--          </el-table-column>-->
          <!--          <el-table-column-->
          <!--              prop="sign"-->
          <!--              label="">-->
          <!--          </el-table-column>-->
          <!--          <el-table-column-->
          <!--              label="">-->
          <!--            <template slot-scope="scope">-->
          <!--            <span-->
          <!--                style="font-weight:bold;width:90px;margin-right: 20px;display: inline-block;">{{scope.row.video}}</span>-->
          <!--              <el-button @click="handleClick(scope.row)" type="primary" size="small"></el-button>-->
          <!--            </template>-->
          <!--          </el-table-column>-->
          <!--          <el-table-column-->
          <!--              label="">-->
          <!--            <template slot-scope="scope">-->
          <!--              <el-button @click="handleClick(scope.row)" type="primary" size="small"></el-button>-->
          <!--              <el-switch style="margin-left: 20px;"-->
          <!--                         v-model="scope.row.value"-->
          <!--                         active-color="#13ce66"-->
          <!--                         inactive-color="#d7d7d7" @change="operation(scope.row)">-->
          <!--              </el-switch>-->
          <!--            </template>-->
          <!--          </el-table-column>-->
        </el-table>
      </el-col>
      <!--page-->
      <el-col :span="24" style="text-align: right;margin-top: 30px">
        <el-pagination
            background
            layout="prev, pager, next"
            :page-size="limit"
            :total="total"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange">
        </el-pagination>
      </el-col>
    </div>

  </div>
</template>

<script>
  import uploadFile from "../../components/uploadFile/index";
  import UpLoadPop from "../material/pop/upLoadPop";
  import LightBtn from "../../components/statusLight/lightBtn";
  import statusLight from "../../components/statusLight/index";
  import mountNum from "../../components/mountNum/index";

  export default {
    name: "index",
    components: {
      uploadFile, UpLoadPop, LightBtn,
      statusLight, mountNum,
    },
    data() {
      return {
        numsInfo: [{value: '', key: '',},],
        input: '',
        picInfos: [],// 
        // count: null,
        dialogVisible: false,
        imageUrl: '',
        pic: [],
        picList: [],
        type_id: null,
        video_url: '',
        withdraw: false,
        // 
        inputSearch: '',// 
        equipment: false,
        btns: [
          {status: 2, value: ''},
          {status: 1, value: ''},],// 1,2,3,
        tableData: [],
        total: null, //
        multipleSelection: [],

        //
        pageSize: 1,
        limit: 5,
        typecontrol_id: '',
        status: null,

        //
        deleteData: [],
        disabled: true,
      }
    },
    mounted() {
      this.nickName()
    },
    methods: {
      // 
      async nickName() {
        console.log('this.page', this.pageSize)
        this.$api.headImage({
            page: this.pageSize,
            limit: this.limit,
            typecontrol_id: this.type_id,
            status: this.status,
          },
          {
            params: {},
          })
          .then((res) => {
            this.tableData = []
            console.log(res)
            this.tableData = res.data.data.list
            // this.count = res.data.data.count
            this.total = res.data.data.count
            this.numsInfo[0].key = res.data.data.count
          })
        // .catch((error) => {
        //   this.$message.error('')
        // })
      },
      typeId(i) {
        this.type_id = i
        console.log(this.type_id)
        this.nickName()
      },
      async handleAvatarSuccess(res, file) {
        // this.imageUrl = URL.createObjectURL(file.raw);
        if (res.status == 200) {
          this.picList.push(res.data)
          console.log(this.picList)
          this.pic = this.picList.toLocaleString()
          console.log(this.pic)
        } else {
          this.$confirm(res.msg)
            .then(_ => {
              done();
            })
            .catch(_ => {
            });
        }

      },
      beforeAvatarUpload(file) {
        const isJPG = file.type.split('/')[0] === 'image';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error('!');
        }
        if (!isLt2M) {
          this.$message.error(' 2MB!');
        }
        return isJPG && isLt2M;
      },
      visible() {
        this.dialogVisible = true
      },
      handleClose(done) {
        done();
      },
      async sure() { // 
        await this.upLoadAdd()
        await this.nickName()
      },
      async upLoadAdd() {
        console.log(!this.pic)
        console.log(this.type_id)
        if (!this.pic || !this.type_id) {
          this.$confirm('')
            .then(_ => {
              done();
            })
            .catch(_ => {
            });
        } else {
          this.$api.head_image_add({
              image: this.pic,
              typecontrol_id: this.type_id,
            },
            {
              params: {},
            }).then((res) => {
            console.log(res)
            this.dialogVisible = false
            // this.nickName()
            this.$router.go(0)
          })
          // .catch((error) => {
          //   this.$message.error('')
          // })
        }
      },
      lightByValue(i) {
        this.status = i.status
        console.log(i.status)
        this.withdraw = i.status === 1;
        this.nickName()
      },
      // 
      handleSizeChange(val) {
        console.log(` ${val} `);
      },
      // 
      handleCurrentChange(val) {
        console.log(`: ${val}`);
        this.pageSize = val
        this.nickName()
      },
      handleClick(row) {
        console.log(row);
      },
      //
      deleteFromData(row) {
        console.log(this.deleteData.toLocaleString())
        let headimage_ids = this.deleteData.toLocaleString()
        this.$api.head_image_del({
            headimage_ids,
          },
          {
            params: {},
          })
          .then((res) => {
            console.log(res)
            this.nickName()
          })
        // .catch((error) => {
        //   this.$message.error('')
        // })
      },
      //
      async recall() {
        await this.headModify()
        await setTimeout(() => {
          this.nickName()
        }, 1000)
      },
      headModify() {
        let headimage_ids = this.deleteData.toLocaleString()
        this.$api.headModify({
          headimage_ids,
        }, {
          params: {},
        })
          .then((res) => {
            console.log(res)
            this.$message.success(res.data.msg)
          })
          .catch()
      },
      handleSelectionChange(val) {
        // console.log(val)
        this.deleteData = []
        val.forEach((e) => {
          this.deleteData.push(parseInt(e.headimage_id))
        })
        this.disabled = !val[0]
      },
      operation(val) {
        console.log(val)
      },
      search() {
        // this.nickName(val,5,this.)
      }
    },
  }
</script>

<style lang="less" scoped>
  .equipment {
    padding: 70px;

    .title01 {
      margin-top: 10px;
      font-size: 18px;
      display: inline-flex;
      align-items: center;

      span {
        width: 126px;
        margin-right: 10px;
      }
    }

    .all {
      justify-content: center;
      background-color: #d7d7d7;
      width: 100%;
      margin-left: 10px;
      padding: 10px;
      display: flex;
      align-items: center;
      border: 1px solid #7a7a7a;
      border-radius: 10px;

      span {
        text-align: center;
        margin: 0;
      }
    }
  }
</style>
